/* Keyframes for bounce animation */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0); /* Starting and ending position */
  }
  50% {
    transform: translateY(-5px); /* Move up by 20px */
  }
}

.co{
/* color:rgb(16, 195, 255);   */
animation: bounce 2s infinite; /* 2 seconds duration, infinite loop */
}
.n{
/* color:rgb(197, 197, 14); */
animation: bounce 2s infinite; /* 2 seconds duration, infinite loop */
}
.ve{
/* color: rgb(63, 62, 62); */
